import React, { Component } from 'react';
import GridBox from '../GridBox/GridBox'
import GridScript from './components/GridScript/GridScript'
import GridOption from './containers/GridOption/GridOption'
import { Element } from 'react-scroll'
import { connect } from 'react-redux'

class DemoBox extends Component {
  render(){
      this.props.dispatch({ type: "TOGGLE_OPTION", demoID: this.props.demoID, contentID: 0 })  //  initial dispatch to get data
      return(
        <Element name={"demo-" + this.props.demoID} className="demo-container-wrapper">

            <h1> {this.props.demoHeader} </h1>
            <GridOption
              options={this.props.options}
              optionID={"option-" + this.props.demoID}
              demoID={this.props.demoID}
            />

            <div className="demo-container">

              <GridBox
                gridID={"grid-" + this.props.demoID}
                demoID={this.props.demoID}
                />
              <GridScript
                boxContent={this.props.boxContent}
                scriptID={"script-" + this.props.demoID}
                demoID={this.props.demoID}
                  />
            </div>

            <p className="grid-description">
              {this.props.children}
            </p>

        </Element>

    );
  }
}

export default connect()(DemoBox);
