import React, { Component } from 'react';
import TabBar from './TabBar';
import { connect } from 'react-redux'
// import BoxContent from '../../../../json/BoxContent.json'
import BoxContent from '../../../../json/BoxContentSource.js'
import src_css from "highlight.js/styles/atom-one-dark-reasonable.css"
import Highlight from 'react-highlight'

class GridBox extends Component {
  curr_html = null
  cur_css = null

  state = {
    selectedCodeContent: "CSS"
  }

  isTriggered = () => {
    return this.props.triggeredDemoID === this.props.demoID
  }

  buttonSelectCallBack = (contentID) => {
    this.setState({selectedCodeContent: contentID})
  }

  getHighlightedContents = (name, raw_content) => {
    return(
      <Highlight className= {'language-'+name}>
        {raw_content}
      </Highlight>
    )
  }

  getContents = (language, content) => {
     if ( language === "HTML"){
      this.curr_html = content.html
      this.curr_css = content.css
      return this.getHighlightedContents("html", this.curr_html)
    } else {
      this.curr_html = content.html
      this.curr_css = content.css
      return this.getHighlightedContents("css", this.curr_css)
    }

  }

  renderCodeContent = () => {
    let contentScript = this.props.contentScript              // what is displayed
    let selectedLanguage  = this.state.selectedCodeContent    // html or css
    if (contentScript && this.isTriggered() && selectedLanguage ){
       return this.getContents(selectedLanguage, contentScript)
    } else {
       return this.getContents(selectedLanguage, {html: this.curr_html, css: this.curr_css})
    }
  }

  render(){
    return(
        <div className="script-block">
          <TabBar callbackFromParent={this.buttonSelectCallBack} tabID={this.props.scriptID} />
          <div className="code-content" src={src_css}>
            {this.renderCodeContent()}
          </div>
        </div>

    );
  }
}

const mapStateToProps = (state, ownProps) => ({
  contentScript: state.options.contentScript,
  triggeredDemoID: state.options.triggeredDemoID
});

export default connect(mapStateToProps)(GridBox);
