import React, { Component } from 'react'
import { connect } from 'react-redux'
import ReactHtmlParser, { processNodes, convertNodeToElement, htmlparser2 } from 'react-html-parser';

class GridBox extends Component {
  curr_element = null

  // check if the user trigger the id
  isTriggered = () => {
    return this.props.triggeredDemoID === this.props.demoID
  }
  reRenderGrid = () => {
    // if (this.props.contentScript) {
    //   console.log("content Script: "+ this.props.contentScript.html)
    // }

    // console.log("triggered demo id: "+ this.props.triggeredDemoID)
    if(this.props.contentScript && this.isTriggered() ){
      // console.log(this.props.triggeredDemoID[0]);
      let currentBlockContainer= document.getElementById(this.props.gridID);
      // remove the previous class if the click button is the same gridID
      if (this.props.gridID && currentBlockContainer){
        let currentGridContainer = currentBlockContainer.childNodes[1]
        let indexOfRemovedContent = currentGridContainer.classList.length - 1  // find the index of deleted element
        if (indexOfRemovedContent)
          currentGridContainer.classList.remove(
            currentGridContainer.classList.item(indexOfRemovedContent))  // remove the last element
        currentGridContainer.classList.add(this.props.contentScript.contentID)
      }
    }
  }

  render(){
    let contentScript = this.props.contentScript
    var new_element
    // this.props.dispatch({ type: "TOGGLE_OPTION", demoID: this.props.demoID, contentID: 0 })  //  initial dispatch to get data
    // console.log(contentScript)
    // console.log(this.isTriggered())
    if (contentScript && !this.isTriggered() && this.curr_element == null){
      new_element = <div className="grid-block-outer">{ ReactHtmlParser(contentScript.html_react) }</div>
      this.curr_element = new_element // assign the current element to the new element
    } else if (contentScript && this.isTriggered()){    // if contentScript is triggered by itself
      new_element = <div className="grid-block-outer">{ ReactHtmlParser(contentScript.html_react) }</div>
      this.curr_element = new_element // assign the current element to the new element
    } else {        // if contentScript is triggered by another element
      return this.curr_element
    }


    // this.reRenderGrid()
    return new_element;
  }
}

// get the contentID from state and assign to this.props.contentID
const mapStateToProps = (state, ownProps) => ({
  contentScript: state.options.contentScript,
  triggeredDemoID: state.options.triggeredDemoID
});

export default connect(mapStateToProps)(GridBox);
